@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Signika:wght@700&display=swap);
body, html {
  margin: 20;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

.container {
  /*     background: linear-gradient(0.25turn, #1d1d1c, #4e4e4d, #acadad); */
  margin: auto 5rem auto 5rem;
}

/* .page-title {
  display: flex;
  justify-content: center;
  text-align: center;
} */


.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  max-width: 800px;
  margin: 3rem auto 1.5rem auto;
  text-align: left;
}

.blog-cards {
  margin: 1.5rem 3rem;
}

.blogs {
  margin-top: -8rem;
}

.title {
  color: gray;
}

.blog-image {
  width: 100%;
  max-height: 500px;
}

.location-date-time {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0 0.5rem 0;
}

.blog-entry {
  padding-top: 1rem;
}

.blog-container {
  margin: 1rem 18rem 1rem 18rem;
}

img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.text-countries {
  font-family: "Krona One", sans-serif;
  font-size: 3em;
  color: #019fc9;
  text-align: center;
}

.text-countries-hr {
  border-color: #019fc9;
}

/*

/ navbar /

/ footer */
.footer-copyright {
  text-align: center;
  /*   padding: 2% 15%; */
}

.footer {
  /* background: linear-gradient(0.25turn, #1d1d1c, #4e4e4d, #acadad); */
  background: linear-gradient(
    90deg,
    rgb(22, 160, 187) 0%,
    rgb(94, 180, 196) 50%,
    rgba(0, 186, 223, 1) 100%
  );
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem 0.5rem 1rem;
  color: black;
}

button {
  border: solid 1px black;
  background-color: transparent;
  /* border-radius: 50%; */
}

.morepadding {
  margin-top: 8em;
}

.about-wrapper {
  /* background-color: coral; */
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.about-profile {
  /* background-color: blue; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.about-profile-names {
  margin-top: 0.25em;
  font-family: "Krona One", sans-serif;
  font-size: 3vw;
  color: #019fc9;
}

.about-cards {
  width: 15vw;
  margin: 0px 0%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  margin-bottom: 1em;
}

.about-text {
  text-align: center;
  font-weight: bold;
  margin: 2em;
}

.about-photo {
  width: 20vw;
/*   height: 16vh; */
  border-radius: 2em;
  border: 2px;
}

.shadowy {
  box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.2);
}

.bg {
    height: 40em;
    background-image: url("http://images.ctfassets.net/t0fd7bi3x7x9/6szfAF5nogylaoK8Tswjkp/21543513b99a936c3c13ead861f09fdc/_RJD0683.jpg");
    background-repeat: no-repeat;
    background-size: cover;
}

.banner{
    /* flex: 1; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: rgb(247, 243, 241);
    -webkit-filter: drop-shadow(0px 4px 0.55rem rgb(230, 19, 19));
            filter: drop-shadow(0px 4px 0.55rem rgb(230, 19, 19));
    padding-top: 15rem;
}

.banner-title{
    font-size: 5em;
    letter-spacing: 4px;
    font-weight: bold;
    text-transform: capitalize;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.banner-subtitle{
    font-size: 2rem;
    letter-spacing: 1px;
    margin-top: -1.5rem;
/*     font-weight: bold; */
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

p {
    font-size: 1.15rem;
    text-align: justify;
}
.sticky-nav {
    position: fixed;
    top: 0;
    z-index: 100;
}

.nav {
/*     background-color: #ffffff2c; */
    top:0;
    position:fixed;
    height: 120px; 
    width:100%;
    z-index: 100;
    transition-timing-function: ease-in;
    transition: 1s;
  }

  .nav_blue {
      background-color: #ffffff;
      /* background: linear-gradient(180deg, rgba(255,255,255,1) 50%, rgba(0,186,223,1) 400%); */
      box-shadow: 0 4px 5px 0 rgba(0,0,0,0.2);
  }

  .nav-main {
    padding: 0.5rem 3rem !important;
  }
  
  .nav_height_only {
    height: 60px;
  }

  .nav-items {
    color: #019fc9;
    font-weight: bold;
    font-size: large;
    padding: 0 0.5rem 0 0.5rem;
    text-transform: uppercase;
  }
  
  .nav-link-color {
    font-family: "Krona One", sans-serif;
    /*   font-family: 'Signika', sans-serif; */
    color: #019fc9;
  }
  
  .nav-link-color-about {
    font-family: "Krona One", sans-serif;
    /*   font-family: 'Signika', sans-serif; */
    color: #d62e17;
  }

  .logo_smaller {
    height: 50px;  
    transition-timing-function: ease-in;
    transition: .5s;
    margin: -2em;
   }
